}

.MainDiv {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    -webkit-filter: drop-shadow(0rem 0rem rem #0eb1c794);
            filter: drop-shadow(0rem 0rem rem #0eb1c794);
}

.MainDiv > .SecondDiv {
    margin-top: 2rem;
    width: 100%;
    height: 60vh;
    display: flex;
    align-items: center;
    grid-template-columns: repeat(2, 1fr);
    flex-direction: column;
    justify-content: space-around;
    position: relative;
    margin-top: 1rem;
}

.SecondDiv > .Heading {
    grid-column: span 2;
    font-size: clamp(1.8rem, 3vw, 2.8rem);
    margin: 1rem 0rem;
    color: #0EB1C7;
    margin-top: 0rem;
    font-weight: 700;
}

.IntroductionText > span {
    font-size: clamp(1.8rem, 3vw, 2.8rem);
}

.SecondDiv > .IntroductionText {
    grid-row: span 1.2;
    font-size: clamp(1.5rem, 3vw, 1.6rem);
    margin: 1rem 0rem;
    color: #0EB1C7;
    display: grid;
    margin-left: max(-30%, -5rem);
    text-align: center;
    padding-inline: 3rem;
}

.IntroductionText > span {
    font-size: clamp(1.8rem, 3vw, 2.8rem);
    text-align: left;
    margin-bottom: -1rem;
}

.Logoimg{
    -webkit-filter: drop-shadow(0px 0px 50px #0EB1C7);
            filter: drop-shadow(0px 0px 50px #0EB1C7);
    width: clamp(12rem, 22vw, 22rem);
    aspect-ratio: 1;
}

.Button_div {
    display: grid;
    place-items: center;
    position: relative;
}
 .HireButton {
    background: transparent;
    width: 20rem;
    padding-top: .8rem;
    padding-bottom: .8rem;
    border-radius: 1rem;
    border: 4px solid #0EB1C7;
    color: #0EB1C7;
    box-shadow: 0px 0px 10px 0px #0EB1C7;
}

.HireButton:hover {
    box-shadow: 0px 0px 10px 3px #0EB1C7;
