.IntroductionMainDiv
{
    width: 100%;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    filter: drop-shadow(0rem 0rem 10rem #0EB1C7);
}
.IntroductionMainDiv >.IntroductionChilDiv
{
    margin-top: 2rem;
    width:70%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
   
   
}
.IntroductionChilDiv > nav
{
    margin-top: 3rem;
    font-size: clamp(1.8rem,3vw,3rem);
    margin-top:1rem;
    margin-bottom:0.5rem ;
    color: #0EB1C7;
    margin-bottom: 2rem;
    font-weight: bold;
} 
.IntroductionChilDiv > .para
{
    width: 60%;
    margin-top: 0rem;
    grid-row: span 1.2;
    font-size: clamp(1.6rem,3vw,2rem);
    margin:1rem 0rem;
    color: #0EB1C7;
    display: grid;
    margin-bottom: 1rem;
  
    text-align: center;
    padding-inline:3rem;
}
.para > span
{
    color: rgb(7, 224, 206);
    font-weight: 400;
}

@media only screen and (min-width:430px)
{
    .IntroductionMainDiv >.IntroductionChilDiv
    {
       width: 100%;

    }
}