@import url("https://fonts.googleapis.com/css2?family=Mea+Culpa&display=swap");

.hireform {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    min-height: 40vh;
    color: #0EB1C7;
    text-transform: capitalize;
}

.hireform > .top {
    text-align: center;
}

.top > heading {
    font-size: clamp(6rem,5vw,10rem);
    font-weight: bold;
    font-family: 'Mea Culpa', cursive;
}
.top > p
{
    font-size: clamp(1rem,2vw,1.8rem);
}
.hireform > .hireme {
    width: 50%;
}

.hireme > ul {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    list-style: none;
    align-items: center;
}

ul > li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   
}
li > label
{
    font-size:  clamp(1.6rem,2.3vw,2.5rem);
     
}
li > input
{
    background-color:transparent;
    padding: .3rem 1rem;
    outline: none;
    color: #0EB1C7;
    border: 1px solid #0EB1C7;
    border-radius: 3px;
    padding:1rem 2rem;
    font-size: 1.5rem;
}
ul > .hireform_button
{
    background: transparent;
     width: 10rem;
     padding-top: .8rem;
     padding-bottom: .8rem;
     border-radius: 1rem;
     border:4px solid #0EB1C7 ;
     color: #0EB1C7 ;
     box-shadow: 0px 0px 10px 0px #0EB1C7; 
}
.hireform_button:active
{
    background-color: #0EB1C7;
    color:blueviolet;
    font-weight: bold;
}
