@import url('https://fonts.googleapis.com/css2?family=Mea+Culpa&display=swap');
.Footer
{
    width: 100%;
    min-height: 30vh;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: space-around;
    grid-template-columns: repeat(1,1fr);
   margin:3rem,0rem;
   color: #0EB1C7 ;
   text-align: center;
   
}
.Footer > .check >.Brand
{
    font-size: clamp(3.5rem, 6vw, 5rem);
    font-weight: bold;
    font-family: 'Mea Culpa', cursive;
   
}
    
.Footer > .check> .Footer_para
{
    margin-inline: 3rem;
    color: #0EB1C7;
    font-size: clamp(1.4rem,2vw,1.8rem);
    text-align: center;
    text-transform:inherit;
    text-indent: 2vw;
}
.check > .Footerdiv
{
    margin: 1rem 0rem;
    display: flex;
    gap: .5rem;
    align-items: center;
    justify-content: center;
    color: #0EB1C7;
    margin-top: 1rem;
}
.Footerdiv >a > img
{
   width: 25px;
   filter: drop-shadow(0px 0px 5px  rgba(25, 245, 190, 0.6));
}