@import url('https://fonts.googleapis.com/css2?family=Mea+Culpa&display=swap');
.Exciting
{
    width: 100%;
    display: grid;
    place-items: center;
    grid-template-columns: repeat(1,1fr);
    min-height: 40vh;
    
}

.Exciting > .ExcitingHeadinng
{
    color:#0EB1C7;
    font-size: clamp(2rem,4vw,4rem);
    font-weight: 400;
       text-transform: capitalize;
       margin-inline: 1rem;
       text-align: center;
}
.Exciting >.PPara
{
    width:50%;
    margin-inline: 3rem;
    padding: 3rem;
    color: #0EB1C7;
    font-size: clamp(1rem,2vw,1.8rem);
    text-align: center;
    text-transform:inherit;
    text-indent: 2vw;
}
.seCond
{
    font-family: 'Mea Culpa', cursive;
}
a > .Bttn
{
    background: transparent;
     width: 20rem;
     padding-top: .8rem;
     padding-bottom: .8rem;
     border-radius: 1rem;
     border:4px solid #0EB1C7 ;
     color: #0EB1C7 ;
     box-shadow: 0px 0px 10px 0px #0EB1C7; 
     margin-bottom: 2rem;
}
.Bttn:hover
{
    box-shadow: 0px 0px 10px 3px #0EB1C7;

}
.Bttn:active
{
 background:#0EB1C7;
 color:rgba(186, 0, 217, .47);
 box-shadow: none;
 font-weight:bolder;
}
