@import url('https://fonts.googleapis.com/css2?family=Mea+Culpa&display=swap');
.Project_form
{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #0b0c10;
    flex-direction: column;
    color: #0EB1C7;
    text-transform: capitalize;
}
.Project_form >.Heading_div
{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Heading_div > .main_Heading
{
    font-size: clamp(6rem,5vw,10rem);
    font-family: 'Mea Culpa', cursive;
    word-spacing: 1.6rem;
}
.Heading_div > .main_Heading2
{font-size: clamp(1rem,2vw,1.8rem);

}
.Project_form >.Project_form_child
{
    width: 50%;
    min-height: 40vh;
}
.lab
{
    font-size: clamp(1rem,2.9vw,2.5rem);
}
label>span
{
   margin-inline-start: .5rem;
   color: rgb(201, 15, 70);
}
.Project_form_child
textarea
{
    background-color: transparent;
    border: 1px solid #0EB1C7;
    outline: none;
    border-radius: 2px;
    color: #12da00;
    width: 50%;
    aspect-ratio: 2;
    background: transparent;
    font-size: 1.5rem;
    font-weight: bold;
    
    
}


.I
{
    width: 50%;
    background-color: transparent;
    font-weight: bold;
}


.Project_button
{
    background: transparent;
    width: 10rem;
    padding-top: .8rem;
    padding-bottom: .8rem;
    border-radius: 1rem;
    border:4px solid #0EB1C7 ;
    color: #0EB1C7 ;
    box-shadow: 0px 0px 10px 0px #0EB1C7; 
}
.Project_button:active
{
    color: blueviolet;
    background-color: #0EB1C7;
    font-weight: bold;
}
@media (max-width:768px)
{
    .Project_form
    {
        justify-content: space-around;
    }
    .Project_form >.Project_form_child
    {
        width: 100%;
        height: 60vh;
        justify-content: center;
        align-items: center;
        
    }
}