.Project {
    width: 70%;
    display: flex;
    flex-direction: column;
    text-align: center; 
    position: relative;
    align-items: center;
    overflow: hidden;
    filter: drop-shadow(0rem 0rem 10rem #0EB1C7);
    margin-top: -5rem;
    
}

.Project > heading {
    font-size: clamp(2rem, 4vw, 4rem);
    color: #0EB1C7;
    margin: 4rem 0rem;
}

.Project > .ProjectDiv {
    display: flex;
    gap: 2rem;
    width: 80%;
    min-height: 70vh;
    align-items:center;
    margin-bottom: 5rem;
    padding: 5rem;
}

.Projects {
    width: 200px;

    /* border: 1px solid #0EB1C7; */
    aspect-ratio: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    color: #0EB1C7;
    align-items: center;
    position: relative;
    overflow: hidden;
    box-shadow: 6px 6px 10px 0px rgba(0, 0, 0, 0.548);
}

.Projects > .Name {
    position: absolute;
    top: 1rem;
}

.Projects > .Base {
    position: absolute;
    top: 5rem;
}

.Projects > img {
    width: 5rem;
    border-radius: 50%;
    aspect-ratio: 1;
}
@media (max-width:768px)
{
    .Project > .ProjectDiv
    {
        flex-direction: column;
    }
}